<template>
  <v-card outlined>
    <v-card-title>
      <v-btn @click="onBack" icon medium class="mr-1">
        <v-icon medium> mdi-arrow-left </v-icon>
      </v-btn>
      <span>Sync Logs</span>
    </v-card-title>
    <v-card-text>
      <v-row no-gutter>
        <v-col cols="12" lg="12" xl="12" md="12" sm="12" xs="12">
          <generic-grid
            :creatable="false"
            :deletable="false"
            :hideActions="true"
            :service="service"
            :gridName="gridName"
            :headers="gridHeaders"
            :itemRowBackground="itemRowBackground"
            itemKey="global_uuid"
            :toolbarTitle="toolbarTitle"
            :extraConfig="extraConfig"
            :isHeaderConfigurable="true"
          >
            <template v-slot:gridItemActions="{ item, refreshGrid }">
              <icon-button
                title="Sync Logs"
                icon="mdi-android-messages"
                color="white"
                :onClick="() => toggleDialog(dialogNames.syncLogs, item, refreshGrid)"
              ></icon-button>
            </template>
          </generic-grid>
        </v-col>
      </v-row>
    </v-card-text>
    <v-dialog persistent max-width="500px" v-if="selectedItem" v-model="dialog[dialogNames.syncLogs]">
      <sync-log-form
        v-if="dialog[dialogNames.syncLogs]"
        :refreshGrid="refreshGridCb"
        :onCancel="() => closeDialog(dialogNames.syncLogs)"
        :item="selectedItem"
      ></sync-log-form>
    </v-dialog>
  </v-card>
</template>
<script>
import SyncLogForm from './syncLogForm.vue';
import IconButton from '../Common/IconButton.vue';
import GenericGrid from '../Common/GenericGrid.vue';
import * as gridNames from '../../constants/gridNames';
import * as syncLogsService from '../../services/syncLogsServices';

const dialogNames = {
  syncLogs: 'syncLogs',
};

export default {
  name: 'sync-logs',
  components: { IconButton, GenericGrid, SyncLogForm },
  data: () => ({
    service: syncLogsService,
    refreshGridCb: f => f,
    selectedItem: null,
    toolbarTitle: 'Sync Logs',
    gridName: gridNames.SYNC_LOGS_GRID,
    extraConfig: {
      hasActions: false,
    },
    gridHeaders: [
      {
        value: 'global_uuid',
        text: 'Global UUID',
        align: 'center',
        sortable: false,
      },
      {
        value: 'last_sync_id',
        text: 'Last Sync',
        sortable: true,
        align: 'center',
      },
      {
        value: 'max_sync_id',
        text: 'Max Sync ',
        sortable: true,
        align: 'center',
      },
      {
        value: 'gridItemActions',
        text: 'Actions',
        align: 'center',
      },
    ],
    dialogNames: dialogNames,
    dialog: {
      [dialogNames.syncLogs]: false,
    },
  }),
  computed: {},
  methods: {
    onBack() {
      this.$router.go(-1);
    },
    itemRowBackground(item) {
      return Number(item.last_sync_id) > Number(item.max_sync_id) && Number(item.max_sync_id) !== 0 ? 'background-yellow': '';
    },
    toggleDialog(dialogName, item, refreshGrid) {
      this.selectedItem = item;
      this.dialog[dialogName] = !this.dialog[dialogName];
      this.refreshGridCb = refreshGrid;
      if (this.dialog[dialogName]) {
        this.selectedItem = item;
      } else {
        this.selectedItem = null;
      }
    },
    closeDialog(dialogName) {
      this.dialog[dialogName] = false;
      this.selectedItem = {};
    },
  },
};
</script>
