<template>
  <v-card :loading="isFetchingSyncLog">
    <v-card-title class="mb-5">
      <span class="headline">Sync Log</span>
    </v-card-title>
    <v-card-text>
      <v-form ref="form" v-model="valid" lazy-validation>
        <v-row dense>
          <v-col cols="12">
            <v-text-field
              required
              @keydown="e => onIntegerKeyDown(e)"
              :disabled="isFetchingSyncLog"
              v-model="syncLog.last_sync_id"
              type="number"
              :min="0"
              label="Last Sync ID"
              :rules="[rules.required]"
            ></v-text-field>
          </v-col>
        </v-row>
      </v-form>
    </v-card-text>
    <form-error :error="error"></form-error>
    <v-card-actions>
      <v-btn v-if="onCancel" color="darken-1" text @click="onCancel"> Cancel </v-btn>
      <v-btn color="blue darken-1" text @click="updateSyncLog" :loading="isUpdatingSyncLog"> Save </v-btn>
    </v-card-actions>
  </v-card>
</template>
<script>
import * as syncLogService from '../../services/syncLogsServices';
import FormError from '../Common/FormError.vue';
export default {
  name: 'sync-log-form',
  props: ['onCancel', 'refreshGrid', 'item'],
  components: { FormError },
  data: () => ({
    valid: false,
    hasError: false,
    error: {},
    isFetchingSyncLog: false,
    isUpdatingSyncLog: false,
    syncLog: {
      last_sync_id: 0,
    },
    rules: {
      required: value => !!value || 'Required.',
    },
  }),
  mounted() {
    this.isFetchingSyncLog = true;
    const globalUUID = this.getGlobalUUID();
    syncLogService
      .fetchSyncLog(globalUUID)
      .then(value => {
        this.syncLog = {
          last_sync_id: Number(value.last_sync_id),
        };
      })
      .catch(err => {
        this.hasError = true;
        this.error = err;
      })
      .finally(() => {
        this.isFetchingSyncLog = false;
      });
  },
  methods: {
    getGlobalUUID() {
      return this.item.global_uuid;
    },
    onIntegerKeyDown(e) {
      if (e.key === '.') {
        e.preventDefault();
      }
    },
    updateSyncLog() {
      this.isUpdatingSyncLog = true;
      syncLogService
        .patchSyncLog(this.getGlobalUUID(), this.syncLog)
        .then(val => {
          this.$toasted.show('Successfully update sync log');
          this.onCancel();
        })
        .finally(() => {
          this.isUpdatingSyncLog = false;
        });
    },
  },
};
</script>
